  line-height: 72px;
  color: #000000;
  text-align: center;
}

.split-functions__nav-list {
  padding: 0;
  margin: 0;
  display: flex;
  justify-content: center;
}

.split-functions__nav-item {
  position: relative;
  padding-bottom: 32px;
  font-size: 15px;
  line-height: 17px;
  color: #000000;
  list-style: none !important;
}

.split-functions__nav-item.js--active::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #00adef;
}

.split-functions__nav-item:not(:last-child) {
  margin-right: 132px;
}

.split-functions__nav-item span {
  transition: all 0.2s ease-in-out;
}

.split-functions__nav-item span:hover {
  color: #00adef;
  cursor: pointer;
}

.split-functions__content {
  display: none;
  padding-bottom: 90px;
}

.split-functions__content.js--show {
  display: block;
}

.split-functions__list {
  margin: 0;
  padding: 0 90px;
  display: flex;
  flex-wrap: wrap;
}

